<template>
    <div id="add-user-in-group">
      <h3>
        <van-icon
          name="arrow-left"
          color="rgb(107, 107, 107)"
          @click="routerGoBack"
        />
        <span>聊天成员({{friendsBasicList.length}})</span>
      </h3>
      <div class="concent-top">
        <div class="search">
          <van-icon name="search" class="search-icon" />
          <!-- @input="onSearch" -->
          <input
            class="input"
            v-model="inputValue"
            type="text"
            placeholder="搜索"
          />
        </div>
        <!-- <p>聊天成员({{friendsBasicList.length}})</p> -->
  
        <div class="friend-list">
          <van-checkbox-group
            v-model="selectBuildGroupFriends"
            :max="1"
            direction="horizontal"
          >
            <div
              v-for=" item in noInGrop"
              :key="item.phone"
              class="friend-item"
              v-show=" item.phone!== userId"
            >
              <van-checkbox :name="item.phone" v-model="checked"  ></van-checkbox>
              
              <div class="img">
                <img :src="showImg(item.avatarurl)" alt="head" />
              </div>
              <div class="desc">
                <span>{{ item.nickname || item.phone }}</span>
              </div>
            </div>
          </van-checkbox-group>
        </div>
      </div>
  
      <p class="button" v-show="showSureButton">
        <button @click="isAdduserInGropu">
          删除({{ selectBuildGroupFriends.length }})
        </button>
      </p>
    </div>
  </template>
    
    <script>
  import WebIM from "../../utils/WebIM";
  import { mapState } from "vuex";
  export default {
    data() {
      return {
        inputValue: "",
        friendsBasicList: [],
        selectBuildGroupFriends: [],
        showSureButton: false,
        checked: true,
        isGroupMenber: [],
        isContactsMenber: [],
        showData: [],
        noInGrop: [],
        isGetFriendsBasicList: false,
        isGetnoInGrop: false,
        isGetAllData: false,
        ownerId:''
      };
    },
    created() {
      if (this.userId) {
        this.getFriendsBasicList();
        this.isInGroup();
      }
    },
    computed: {
      ...mapState({
        userId: (state) => state.user.userId,
  
        newCreateGroupId: (state) => state.home.newCreateGroupId,
        // 群id
        cacheClickGroupId: (state) => state.home.cacheClickGroupId,
      }),
    },
    watch: {
      userId() {
        if (this.userId) {
          this.getFriendsBasicList();
          this.isInGroup();
        }
      },
      
  
      isGetFriendsBasicList() {
        if (this.isGetFriendsBasicList && this.isGetnoInGrop) {
          this.isGetAllData = true;
        }
      },
      isGetnoInGrop() {
        if (this.isGetFriendsBasicList && this.isGetnoInGrop) {
          this.isGetAllData = true;
        }
      },
      isGetAllData(newV) {
        if (newV) {
         
        }
      },
      selectBuildGroupFriends(newV) {
        if (this.length !== 0) {
          this.showSureButton = true;
        } else {
          this.showSureButton = false;
        }
      },
    },
    methods: {
      routerGoBack() {
        this.$router.go(-1);
      },
      getFriendsBasicList() {
        WebIM.conn.getContacts(this.$store.state.user.userId).then((res) => {
          res.data.map((item, i) => {
            WebIM.conn.fetchUserInfoById(item).then((obj) => {
              this.friendsBasicList.push(obj.data[item]);
              //获取练习人昵称
              this.isContactsMenber.push(obj.data[item].nickname);
              //  console.log(obj.data[item].nickname,1324536);
            });
            if (i == res.data.length - 1) {
              this.isGetFriendsBasicList = true;
            }
          });
          // console.log(this.friendsBasicList,'所有联系人信息');
        });
      },
      showImg(img) {
        if (img != undefined) {
          return img;
        } else {
          return require("../../assets/image/addressImg/head.webp");
        }
      },
      // 踢人出群
      isAdduserInGropu() {
        console.log(this.selectBuildGroupFriends,"hshdhf");
        let id =
          this.cacheClickGroupId !== ""
            ? this.cacheClickGroupId
            : sessionStorage.getItem("groupInfoCacheSave");
  
        WebIM.conn
          .removeGroupMember({
            groupId: id,
            username: this.selectBuildGroupFriends[0],
          })
          .then((res) => {
            console.log(res, 90);
            this.$toast("删除成功");
          })
         
  
       
      },
  
      //已经在群的人
      isInGroup() {
        let id =
          this.cacheClickGroupId !== ""
            ? this.cacheClickGroupId
            : sessionStorage.getItem("groupInfoCacheSave");
        let option = {
          pageNum: 1,
          pageSize: 200,
          groupId: id,
        };
        //获取群成员列表
        WebIM.conn.listGroupMembers(option).then((res) => {
          let newArr = res.data;
          //  console.log(newArr,'群里的人');
          newArr.map((item, index) => {
            if(item.owner) {
              this.ownerId = item.owner
            }
           //  console.log( this.ownerId,123,);
            // 获取群成员信息
            WebIM.conn
              .fetchUserInfoById(item.owner ?? item.member)
              .then((res) => {
                //昵称
                let name = res.data[item.owner ?? item.member].nickname;
                this.isGroupMenber.push(name);
                this.noInGrop.push(res.data[item.owner ?? item.member]);
                console.log( this.noInGrop,124115,this.userId);
                if (index == newArr.length - 1) {
                  this.isGetnoInGrop = true;
                }
              });
          });
        });
      },
  
     
    },
  };
  </script>
    
    <style lang="scss" scoped>
  #add-user-in-group {
    position: relative;
    width: 100vw;
    height: 100vh;
    color: rgb(164, 164, 164);
    z-index: 999;
    background-color: var(--theme-background-black);
    h3 {
      background-color: rgb(67, 67, 67);
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      width: 100%;
      padding: 10px;
      height: 45px;
      span {
        width: 58%;
      }
    }
  
    .concent-top {
      width: 100%;
      .search {
        width: 100%;
        height: 50px;
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        align-items: center;
        position: relative;
        color: white;
        padding: 5px 0;
        background-color: rgb(41, 41, 41);
        border-bottom: 1px solid var(--theme-cell-black);
        .input {
          width: 75%;
          height: 80%;
          background-color: #494545;
          outline: none;
          border: none;
          text-indent: 30px;
        }
  
        .search-icon {
          color: rgb(164, 164, 164);
          position: absolute;
          left: 12px;
          top: 18px;
          font-size: 18px;
        }
      }
      p {
        height: 45px;
        line-height: 30px;
        width: 100%;
        padding: 8px;
        background-color: rgb(41, 41, 41);
        &:not(:last-child) {
          border-bottom: 1px solid var(--theme-cell-black);
        }
      }
      .friend-list {
        margin-top: 15px;
  
        .friend-item {
          height: 45px;
          line-height: 45px;
          width: 100%;
          background-color: rgb(24, 24, 24);
          display: flex;
          flex-direction: row;
          align-items: center;
          padding-left: 15px;
          .van-checkbox {
            padding-right: 10px;
          }
          .img {
            width: 50px;
            height: 40px;
            border-radius: 5px;
            margin-right: 15px;
  
            img {
              width: 100%;
              height: 100%;
              display: block;
              border-radius: 3px;
            }
          }
  
          .desc {
            width: 90%;
            border-bottom: 1px solid rgba(204, 203, 203, 0.1);
          }
        }
      }
    }
  
    .button {
      position: absolute;
      bottom: 0;
      width: 100%;
      height: 60px;
      padding: 10px;
      text-align: right;
      background-color: rgb(67, 67, 67);
      button {
        outline: 0px;
        border: 0px;
        padding: 8px 15px;
        color: #fff;
        border-radius: 5px;
        background-color: var(--theme-green);
      }
    }
  }
  </style>